<template>
	<view class="register">
		<u-navbar :is-back="false" title-color="#fff" title="动态" :background="background" title-bold>
			<view slot="right" style="margin-right: 30rpx;" @click="torelease">
				<u-icon name="camera-fill" style="margin-right: 7rpx;"></u-icon>发布动态
			</view>
		</u-navbar>
		<view class="formBox">
			<view class="list02" v-for="(item,index) in hdlist" :key="index" v-show="hdlist[0]">
				<u-row>
					<u-col span="2">
						<u-avatar :src="item.header" size="90"></u-avatar>
					</u-col>
					<u-col span="10">
						<p class="titlebox">{{item.pushName}}</p>
						<p class="pbox">{{item.createTime}}</p>
					</u-col>
					<u-col span="12">
						<p class="pbox01 gray">{{item.content}}</p>
					</u-col>
					<u-col span="6" v-for="(k,m) in item.file.split(',')" :key="m"
						style="text-align:center;border-bottom: 1px solid #ECECEC;padding: 10rpx 0 24rpx 0;">
						<image src="/static/img/head.png" mode="widthFix" style="width: 233rpx;"></image>
					</u-col>
					<u-col span="6"
						style="text-align:center;border-bottom: 1px solid #ECECEC;padding: 10rpx 0 24rpx 0;">
						<image src="/static/img/head.png" mode="widthFix" style="width: 233rpx;"></image>
					</u-col>
					<u-col span="12" style="margin-top: 20rpx;">
						<u-tag :text="i" mode="dark" v-for="(i,indx) in item.aboutList" :key="indx" />
					</u-col>
				</u-row>
			</view>
		    <view style="width: 100%;text-align: center;color: #999;font-size: 36rpx;padding-top: 450rpx;" v-show="!hdlist[0]">暂无动态</view>
		</view>
	</view>
</template>

<script>
	import {
		antilistObj
	} from '../../api/index.js'
	export default {
		data() {
			return {
				hdlist: [],
				params: {
					pageNum: 1,
					pageSize: 10
				},
				background: {
					backgroundColor: '#1F61BE',
					color: "#fff"
				},
				list01: [],
			}
		},
		onLoad() {
			antilistObj(this.params).then(res => {
				if (res.code == 200) {
					this.hdlist = res.rows;
				}
			})
		},
		methods: {
			torelease() {
				uni.navigateTo({
					url: 'release'
				})
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.mybox {
		text-align: center;
		padding: 50rpx 0;
	}

	.formBox {}


	.list02 {
		width: 100%;
		height: 500rpx;
		background: #FFFFFF;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
		padding: 26rpx 30rpx 28rpx 30rpx;
		margin-bottom: 20rpx;
		box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.06);
	}

	.list02 .titlebox {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
