		text-overflow: ellipsis;
	}

	.list02 .pbox {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
