		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		overflow: hidden;
		/*文本超出隐藏*/
		display: -webkit-box;
		/*盒子模型微弹性伸缩模型*/
		-webkit-box-orient: vertical;
		/*伸缩盒子的子元素垂直排列*/
		-webkit-line-clamp: 2;
	}

	.list02 .gray {
		color: #333;
	}


